import apiConfigs from '../../static/api/index.js'
import util from '../../static/util/index.js'
export default {
	data() {
		return {
			warrant:0,
			userInfo:{
				nickName:'',
				avatarUrl:'',
				city:'',
				gender:''				
			},
			dataInfo:{
				diffDay:'',
				diffHour:'',
				frequency:''
			}
		}
	},
	onLoad() {
		this.accessPermission();
	},
	methods: {		
		//授权登录
		getPower(){
			let that = this;
			uni.getUserInfo({
			    provider: 'weixin',
			    success: function(res) {
			        // console.log(res.userInfo);
					that.userInfo.avatarUrl = res.userInfo.avatarUrl;
					that.userInfo.nickName = res.userInfo.nickName;
					that.userInfo.city = res.userInfo.city;
					that.userInfo.gender = res.userInfo.gender;
					that.toPower();
			    }
			});
		},
		//授权去登录
		toPower(){
			let that = this;
			uni.request({
				url:apiConfigs.login,
				method:'POST',
				data:that.userInfo,
				success: (res) => {
					if(res.data.ok){
						uni.setStorage({
							key:'userId',
							data:res.data.data.id,
							success: () => {
								that.getMyInfo(res.data.data.id)
							}
						})
						
					}else{
						console.log("授权登录出错！");
					}
				}
			})
		},
		// 判断scope后对应的scope.userInfo是否授权
		accessPermission() {    
			let that = this;
			uni.getSetting({
				success(res) {
					if (res.authSetting['scope.userInfo']) {
						uni.getSetting({
							success(res) {
								uni.getStorage({
									key:'userId',
									success: (res) => {
										that.getMyInfo(res.data);
									},
									fail: () => {
										that.getPower();
									}
								})
							}
						})
					}else{
						uni.getStorage({
							key:'userId',
							success: () => {
								that.warrant = 0;
								uni.clearStorage();
							}
						})
					}
				}
			})
		},
		//获取个人信息
		getMyInfo(val){
			let that = this;
			uni.request({
				url:apiConfigs.info,
				method:'POST',
					data:{userId:val},
				success: (res) => {
					// console.log(res)
					var info = res.data.data;
					if(res.data.ok){
						that.warrant = 1;
						that.userInfo.nickName = info.nickName;
						that.userInfo.avatarUrl = info.headPhoto;
						console.log(new Date(info.time))
						var day = new Date().getTime();
						var result = util.dateDiff(day,info.time);
						that.dataInfo.diffDay = result.day;
						that.dataInfo.diffHour = result.hours;
						that.dataInfo.frequency = info.frequency;
					}else{
						console.log("数据异常！")
					}
				}
			})
		}
	}
}
